import * as path from 'path';
import * as Gulp from 'gulp';
import * as yargs from 'yargs';

import * as coreBuild from '@microsoft/gulp-core-build';

import { initializeTslintTask } from '@microsoft/sp-tslint-rules';
import { typescript, tslint, removeTripleSlash, apiExtractor } from '@microsoft/gulp-core-build-typescript';

import { BuildRig, ITaskDefinition, ICoreBuildArgs } from './BuildRig';
import { BuildRigConstants } from './BuildRigConstants';

export const preCopy: coreBuild.CopyTask = new coreBuild.CopyTask();
export const postCopy: coreBuild.CopyTask = new coreBuild.CopyTask();

export interface ISPBuildRigArgs extends ICoreBuildArgs {
  /**
   * All SPFx build rigs can be tested in a specific locale using this flag,
   * which is defined in the localization code
   *
   * @todo 253519 remove this once this logic is in GCB/the Task definition
   */
  locale: string;

  /**
   * When this flag is enabled, certain expensive build steps will be disabled.
   * This should allow the developer to quickly refresh their repository after
   * pulling from master, or in the inner loop (when doing serve)
   */
  minimal: boolean;
}

/**
 * This class represents the basic shared build rig for all SPFx Rigs. It defines a few
 * simple sub-tasks, and only registers a "build" task.
 */
export class SPBuildRig extends BuildRig {
  protected _preBuildTasks: coreBuild.IExecutable[] = [];
  protected _postTypescriptTasks: coreBuild.IExecutable[] = [];
  protected _postBuildTasks: coreBuild.IExecutable[] = [];

  protected args: ISPBuildRigArgs;

  public initialize(gulp: typeof Gulp): void {
    super.initialize(gulp);
  }

  /**
   * Register additional sub-tasks to run before the typescript subtask.
   * Note, this is meant to be used in a gulpfile.js to inject one-off subtasks
   */
  public addPreBuildTask(tasks: coreBuild.IExecutable | coreBuild.IExecutable[]): void {
    this._addTaskOrListOfTasks(tasks, this._preBuildTasks);
  }

  /**
   * Register additional sub-tasks to run after the typescript subtask.
   * Note, this is meant to be used in a gulpfile.js to inject one-off subtasks
   */
  public addPostTypescriptTask(tasks: coreBuild.IExecutable | coreBuild.IExecutable[]): void {
    this._addTaskOrListOfTasks(tasks, this._postTypescriptTasks);
  }

  /**
   * Register additional sub-tasks to run after the typescript subtask.
   * @deprecated
   */
  public addBuildTasks(tasks: coreBuild.IExecutable | coreBuild.IExecutable[]): void {
    this._addTaskOrListOfTasks(tasks, this._postTypescriptTasks);
  }

  /**
   * Register additional sub-tasks to run after the entire build.
   * Note, this is meant to be used in a gulpfile.js to inject one-off subtasks
   */
  public addPostBuildTask(tasks: coreBuild.IExecutable | coreBuild.IExecutable[]): void {
    this._addTaskOrListOfTasks(tasks, this._postBuildTasks);
  }

  /**
   * Registers the command line arguments which are available for this rig
   * @todo 253519 this code should be moved into gulp-core-build/the task definitions
   */
  protected getYargs(): yargs.Argv {
    return super.getYargs()
      .option('locale', {
        alias: 'l',
        describe: 'override the default culture (e.g. "fr-fr")',
        type: 'string'
      })
      .option('minimal', {
        alias: 'm',
        describe: 'Speeds up the build by running the minimal set of tasks required to produce an executable output',
        type: 'boolean'
      })
      .command(BuildRigConstants.tasks.build, 'build the project')
      .command(BuildRigConstants.tasks.default, 'equivalent to bundle');
  }

  /**
   * Register 2 tasks, build and default, which simply copy files, then run typescript and tslint
   */
  protected getTasks(): Map<string, ITaskDefinition> {
    const result: Map<string, ITaskDefinition> = new Map<string, ITaskDefinition>();

    result.set(BuildRigConstants.tasks.build, { executable: this.getBuildTask() });
    result.set(BuildRigConstants.tasks.default, { executable: this.getBuildTask() });

    return result;
  }

  /**
   * Override this function to overwrite the "build" task
   */
  protected getBuildTask(): coreBuild.IExecutable {
    return coreBuild.serial(
             preCopy,
             coreBuild.serial(this._preBuildTasks),
             this.getCoreBuildTask(),
             coreBuild.serial(this._postBuildTasks),
             postCopy);
  }

  /**
   * Override this function to redefine the core build loop
   */
  protected getCoreBuildTask(): coreBuild.IExecutable {
    return coreBuild.parallel(
      tslint,
      coreBuild.serial(
        coreBuild.serial(typescript, removeTripleSlash, apiExtractor),
        coreBuild.serial(...this._postTypescriptTasks)
      )
    );
  }

  protected setupSharedConfig(): void {
    // Ensure that the SPFx tslint rules are activated
    initializeTslintTask(tslint);

    tslint.mergeConfig({
      displayAsWarning: true
    });

    preCopy.name = 'pre-copy';
    postCopy.name = 'post-copy';

    apiExtractor.mergeConfig({
      apiJsonFolder: path.join(process.cwd(), 'dist')
    });
  }

  /**
   * This function cleans up the shared config by populating task config properties that depend on other tasks'
   *  user-defined properties.
   */
  protected finalizeSharedConfig(): void {
    if (this.args.minimal) {
      this._disableTasks(tslint, apiExtractor);
    }
  }

  private _addTaskOrListOfTasks(tasks: coreBuild.IExecutable | coreBuild.IExecutable[],
                                array: coreBuild.IExecutable[]): void {
    if ((tasks as coreBuild.IExecutable[]).length) {
      array.push(...(tasks as coreBuild.IExecutable[]));
    } else {
      array.push(tasks as coreBuild.IExecutable);
    }
  }
}
